.food-item {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #161616;
}

.food-item h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: 700;
  color: #68b151;
  font-size: 1.25rem;
}

@media screen and (max-width: 430px) {
  .description {
    font-size: 0.7rem;
  }
}
