header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #151515;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.green-title {
  color: #6dd34d;
}

.main-img {
  width: 100%;
  max-height: 23rem;
  z-index: 0;
  overflow: hidden;
}

.main-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 460px) {
  .big-title {
    font-size: 1.5rem;
  }
}
